
























import { getArrayObjIndexByKey } from "@/utils";
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  selectedLabel = '天辉'
  tabs: any = [{
    label: '天辉',
    heroes: ['敌法师', '卓尔游侠', '主宰', '米拉娜', '变体精灵', '幻影长矛手', '复仇之魂', '力丸', '矮人狙击手', '圣堂刺客', '露娜', '赏金猎人', '熊战士', '矮人狙击手', '圣堂刺客', '露娜', '赏金猎人', '熊战士', '矮人狙击手', '圣堂刺客', '露娜', '赏金猎人', '熊战士', '矮人狙击手', '圣堂刺客', '露娜', '赏金猎人', '熊战士', '矮人狙击手', '圣堂刺客', '露娜', '赏金猎人', '熊战士']
  }, {
    label: '夜魇',
    heroes: ['血魔', '影魔', '剃刀', '剧毒术士', '虚空假面', '幻影刺客', '冥界亚龙', '克林克兹', '育母蜘蛛', '编织者', '幽鬼', '司夜刺客', '米波']
  }]
  /**computed */
  get initialIndex() {
    return getArrayObjIndexByKey(this.tabs, { label: this.selectedLabel })
  }
  get options() {
    return {
      pullDownRefresh: {
        threshold: 60,
        stop: 40,
        txt: '更新成功'
      },
      pullUpLoad: {},
      scrollbar: true,
    }
  }

  /**method */
  changePage(current: any) {
    this.selectedLabel = this.tabs[current].label
    console.log('当前轮播图序号为:' + current)
  }
  scroll(pos: any) {
    const x = Math.abs(pos.x)
    const tabItemWidth = (this.$refs.tabNav as any).$el.clientWidth
    const slideScrollerWidth = (this.$refs.slide as any).slide.scrollerWidth;
    const deltaX = x / slideScrollerWidth * tabItemWidth;
    (this.$refs.tabNav as any).setSliderTransform(deltaX)
  }
  onPullingDown() {
    // 模拟更新数据
    setTimeout(() => {
      if (Math.random() > 0.5) {
        // 如果有新数据
        let items: any = this.tabs[this.initialIndex].heroes;
        items.unshift('ddd')
      } else {
        // 如果没有新数据
        const scroll: any = this.$refs.cubeScroll;
        if (scroll) {
          scroll[this.initialIndex].forceUpdate()
        }
      }
    }, 1000)
  }
  onPullingUp() {
    // 模拟更新数据
    setTimeout(() => {
      if (Math.random() > 0.5) {
        // 如果有新数据
        const newPage = ['ss']
        const items: any = this.tabs[this.initialIndex].heroes;
        this.tabs[this.initialIndex].heroes = items.concat(newPage);
      } else {
        // 如果没有新数据
        const scroll: any = this.$refs.cubeScroll;
        if (scroll) {
          scroll[this.initialIndex].forceUpdate()
        }
      }
    }, 1000)
  }
}
